/* large screens */
.toast-dialog {
  background-color: var(--toast-dialog-background-color, var(--color-action-primary, #3665f3));
  border-radius: var(--toast-dialog-border-radius, 16px 16px 0 0);
  bottom: 0;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.28);
  color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  left: 0;
  max-height: 40vh;
  min-width: 320px;
  position: fixed;
  transform: translateY(0);
  width: 100vw;
  will-change: opacity, transform;
  z-index: 2;
}
.toast-dialog a {
  color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
}
.toast-dialog a:focus {
  outline-color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  outline-style: auto;
  outline-width: 1px;
}
.toast-dialog--transition {
  transition: opacity 0.2s cubic-bezier(0.21, 0.31, 1, 1.22) 0s, transform 0.2s cubic-bezier(0.21, 0.31, 1, 1.22) 0s;
}
.toast-dialog--show,
.toast-dialog--hide-init {
  display: block;
  opacity: 1;
  transform: translateY(0);
}
.toast-dialog--show-init,
.toast-dialog--hide {
  display: block;
  opacity: 0;
  transform: translateY(110%);
}
.toast-dialog__window {
  margin: 8px 16px 16px;
}
.toast-dialog__header {
  align-items: center;
  display: flex;
}
.toast-dialog__header h2,
.toast-dialog__title {
  margin: 0;
}
button.toast-dialog__close {
  align-self: center;
  border: 0;
  margin: 0 0 0 auto;
  padding: 0;
}
button.toast-dialog__close > svg,
button.toast-dialog__close:hover > svg,
button.toast-dialog__close:focus > svg {
  fill: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
}
button.toast-dialog__close:focus {
  outline-color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  outline-style: auto;
  outline-width: 1px;
  outline-offset: -8px;
}
.toast-dialog__footer {
  display: flex;
  justify-content: flex-end;
}
.toast-dialog__footer button::first-letter {
  text-decoration: underline;
}
.toast-dialog__footer .btn--primary,
.toast-dialog__footer .btn--secondary {
  border-color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  border-style: solid;
  border-width: 1px;
}
.toast-dialog__footer .btn--primary {
  background-color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  color: var(--toast-dialog-background-color, var(--color-action-primary, #3665f3));
}
.toast-dialog__footer .btn--secondary {
  background-color: transparent;
  color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  font-weight: 700;
  margin-right: 8px;
}
.toast-dialog__footer .btn--primary:focus,
.toast-dialog__footer .btn--secondary:focus {
  outline-color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  outline-style: auto;
  outline-width: 1px;
}
.toast-dialog__footer .btn--primary:focus,
.toast-dialog__footer .btn--primary:hover {
  background-color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  border-color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  border-style: solid;
  border-width: 1px;
}
.toast-dialog__footer .btn--secondary:focus,
.toast-dialog__footer .btn--secondary:hover {
  background-color: var(--toast-dialog-background-color, var(--color-action-primary, #3665f3));
  border-color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
  border-style: solid;
  border-width: 1px;
  color: var(--toast-dialog-foreground-color, var(--color-background-default, #fff));
}
@media (min-width: 601px) {
  .toast-dialog {
    border-radius: var(--toast-dialog-large-border-radius, 16px);
    bottom: 16px;
    left: 16px;
    max-width: 480px;
    width: auto;
  }
  .toast-dialog__window {
    margin: 16px 24px 24px;
  }
}
[dir="rtl"] .toast-dialog__footer .btn--primary {
  margin-right: 7px;
}
